<template>
<div>
    <div style="margin-left:30%;width:40%;margin-top:20%">
    <el-row>
        <el-col :span="2">用户名</el-col>
        <el-col :span="10">
            <el-input v-model="uname"></el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="2">邮箱</el-col>
        <el-col :span="10">
            <el-input v-model="email"></el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="2">验证码</el-col>
        <el-col :span="10">
            <el-input v-model="yanzhengma">
                 <i slot="suffix" class="el-input__icon el-icon-search" @click="getCode"></i>
            </el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="5">
            <el-button  @click="next">前往修改</el-button>
        </el-col>
        <el-col :span="5">
            <el-button>取消</el-button>
        </el-col>
        </el-row>
    </div>
    <div>
    <el-dialog title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <el-row>
        <el-col :span="2">新密码</el-col>
        <el-col :span="10">
            <el-input v-model="npwd1"></el-input>
        </el-col>
        </el-row>
        <el-row>
        <el-col :span="2">确认密码</el-col>
        <el-col :span="10">
            <el-input v-model="npwd2"></el-input>
        </el-col>
        </el-row>
        
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="modify">确 定</el-button>
        </span>
    </el-dialog>
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            uname:'',
            email:'',
            yanzhengma:'',
            code:'',
            upwd:'',
            npwd1:'',
            npwd2:'',
            dialogVisible:false,

        }
    },
    methods:{
        getCode(){
        console.log(this.form)
        this.$axios.post("getCode",this.$qs.stringify({email:this.email})  )
            .then((res)=>{
                this.code =res.data
                console.log("结果："+res.data)
            }).catch((error)=>{
            console.log(error)
        })
      },
        next(){
            if(this.yanzhengma==this.code){
                this.dialogVisible=true
            }else{
                alert("验证码错误")
            }
            
        },
        modify(){
            this.dialogVisible=false
            this.$axios.post('modify',this.$qs.stringify({uname:this.uname,upwd:this.npwd2})).then(
                (res)=>{
                    if(res.data=='修改成功'){
                        alert("修改成功")
                    }else if(res.data=='修改失败'){
                        alert("修改失败")
                    }else{
                        alert("新密码不能与原密码相同")
                    }
                }
            )
        },
     
             handleClose(done){
                this.$confirm('确认关闭？')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {});
                }
        }
}
</script>


