<template>
    <div>
        <el-row></el-row>
        <el-row>
            <el-table :data="dataList">
                <el-table-column type="index" :index="defaultIndex"></el-table-column>
                <el-table-column label="标题"></el-table-column>
                <el-table-column label="发布时间"></el-table-column>
                <el-table-column label="访问量"></el-table-column>
                <el-table-column label="收藏量"></el-table-column>
                <el-table-column label="完成学习人数"></el-table-column>
                <el-table-column label="修改大类"></el-table-column>
                <el-table-column label="修改小类"></el-table-column>
            </el-table>
        </el-row>
        <el-row>
            <div id="footer">
                <el-pagination
                    background
                    :page-size="4"
                    :pager-count="7"
                    :total="100"
                    @current-change="handleCurrentChange"
                    layout="total,prev, pager, next,jumper">
                </el-pagination>
        </div>
        </el-row>
    </div>
</template>
<script>
export default {
    created(){

    },
    data(){
        return{
            page:1,
            dataList:[]
        }
    },
    methods:{
        defaultIndex(index){
            return index+1
        },
        handleCurrentChange(val){
            this.page=val
            console.log(typeof(this.page)+":"+this.page)
        },
    }
}
</script>
<style scoped>
#footer{
position:fixed;
left:20%;
bottom:10px;
height:30px;
width:100%;
}
</style>


