<template>
<div>
    <el-row>
        <el-col :span="3">总积分</el-col>
        <el-col :span="10">
            <el-input v-model="count" disabled></el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="3">总积分排名</el-col>
        <el-col :span="10">
            <el-input v-model="ranking" disabled></el-input>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="3">年度积分</el-col>
        <el-col :span="5">
            <el-input v-model="countYear" disabled></el-input>
        </el-col>
        <el-col :span="3" :offset="2">
            <el-select v-model="year">
                <el-option v-for="(item) in ['2021','2020','2019','2018']" :key="item" :label="item" :value="item"></el-option>
            </el-select>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="3">月积分</el-col>
        <el-col :span="5">
            <el-input v-model="countMonth"  disabled></el-input>
        </el-col>
        <el-col :span="3" :offset="2">
            <el-select v-model="month">
                <el-option v-for="(item) in 12" :key="item" :label="item" :value="item"></el-option>
            </el-select>
        </el-col>
    </el-row>
</div>
</template>
<script>
export default {
    created(){
        var date = new Date
        var dateMonth = date.getMonth()+1
        this.month=dateMonth
        var dateYear = date.getFullYear()
        this.year=dateYear
        console.log("当前月："+this.month+'当前年'+this.year)
    
    },
    data(){
        return{
            month:'',
            year:'',
            ranking:'',//总积分排名
            count:'',//总积分
            countMonth:'',//月积分
            countYear:'',//年积分
        }
    },
    methods:{
        getData(sendData){
            this.$axios.post('queryUserScore',this.$qs.stringify({when:sendData}).then(
                (res)=>{

                }
            )
            )
        }
    },
    watch:{
        month:{
            handler(){
                var sendData=this.year+'-'+this.month
                console.log(sendData)
            },
            immediate:true
        },
        year:{
            handler(){
                var sendData=this.year+'-'+this.month
                console.log(sendData)
            },
            immediate:true
        }
    }
    
}
</script>
<style scoped>
.el-row{
    margin-top:10px
}
</style>


