<template>
    <div>
        <el-row>
            <el-col :span="1">年份</el-col>
            <el-col :span="5">
                <el-select placeholder="年份" v-model="year">
                    <el-option label="全部" value="1998"></el-option>
                    <el-option label="2021" value="2021"></el-option>
                    <el-option label="2020" value="2020"></el-option>
                    <el-option label="2019" value="2019"></el-option>
                    <el-option label="2018" value="2018"></el-option>
                </el-select>
            </el-col>
            <el-col :span="1" :offset="3">月份</el-col>
            <el-col :span="5">
                <el-select placeholder="月份" v-model="month" :disabled="year=='1998'?true:false">
                    <el-option label="全部" value="-"></el-option>
                    <el-option label="1" value="-1"></el-option>
                    <el-option label="2" value="-2"></el-option>
                    <el-option label="3" value="-3"></el-option>
                    <el-option label="4" value="-4"></el-option>
                    <el-option label="5" value="-5"></el-option>
                    <el-option label="6" value="-6"></el-option>
                    <el-option label="7" value="-7"></el-option>
                    <el-option label="8" value="-8"></el-option>
                    <el-option label="9" value="-9"></el-option>
                    <el-option label="10" value="-10"></el-option>
                    <el-option label="11" value="-11"></el-option>
                    <el-option label="12" value="-12"></el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row style="margin-top:30px">
            <el-table :data="tableData">
                <el-table-column label="排名" type="index" :index="defaultIndex"></el-table-column>
                <el-table-column label="排名（手）" prop="a"></el-table-column>
                <el-table-column label="姓名" prop="uName"></el-table-column>
                <el-table-column label="部门"   :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
                    :filter-method="filterBumen" prop="dept">
                </el-table-column>
                <el-table-column label="总积分" prop="uScore" v-if="isAll" sortable></el-table-column>
                <el-table-column label="积分值" prop="sumGetScore" v-else  sortable></el-table-column>
            </el-table>
        </el-row>
        <el-row>
            <div id="footer">
                <el-pagination
                    background
                    :page-size="4"
                    :pager-count="7"
                    :total="100"
                    @current-change="handleCurrentChange"
                    layout="total,prev, pager, next,jumper">
                </el-pagination>
            </div>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            page:1,
            year:'1998',
            month:'-',
            result:'',
            isAll:true,
            tableData:[
                {
                    uId:'1',
                    uName:'包包大人',
                    uScore:'55',
                    dept:'1',
                    sumGetScore:'55'
                },
                {
                    uId:'2',
                    uName:'包包大人2',
                    uScore:'50',
                    dept:'2',
                    sumGetScore:'22'
                },
                {
                    uId:'3',
                    uName:'包包大人3',
                    uScore:'45',
                    dept:'1',
                    sumGetScore:'22'
                }
            ]
        }
    },
    watch:{
        year:{
            handler(){
                if(this.year=='1998'){
                    this.getData()
                    this.isAll=true
                   this.month='-'
                }else{
                    this.isAll=false
                    this.getData()
                    this.month='-'
          
                }
            },
            immediate:true
        },
        month:{
            handler(){
                console.log(this.month)
                if(this.month=='-'){
               
                    this.getData()
                }else{
    
                    this.getData()
                }
            },
            immediate:true
        },
        
    },
    methods:{
         // 根据年份、月份获取数据
        getData(){
            var send = this.year+this.month
            console.log('此次查询数据when---'+send)
            // this.$axiso.post('adminQueryUserScore',this.$qs.stringify({when:send})).then(
            //     (res)=>{

            //     }
            // )
        },
        // value ——选中的筛选值，row每一行
        filterMonth(value, row) {
            return row.d === value;
        },
        filterBumen(value,row){
            return row.c ===value
        },
        filterYear(value, row) {
            return row.e === value;
        },
        defaultIndex(index){
            return index+1
        },
        handleCurrentChange(val){
            this.page=val
            console.log(typeof(this.page)+":"+this.page)
        },
    }
}
</script>
<style scoped>
#footer{
position:fixed;
left:20%;
bottom:10px;
height:30px;
width:100%;
}
</style>


