<template>
    <div>
        <div>
            <el-col :span="9" :offset="3" v-for="(item,index) in bookData.slice((page-1)*4,page*4)" :key="index" style="margin-top:5px">
                <book :bookData="item"  @opena="openee"></book>
            </el-col>
        </div>
        <div id="footer">
                <el-pagination
                    background
                    :page-size="4"
                    :pager-count="7"
                    :total="100"
                    @current-change="handleCurrentChange"
                    layout="total,prev, pager, next,jumper">
                </el-pagination>
        </div>
    </div>
</template>
<script>
import book from '../../other/book.vue'
export default {
    components: { book },
    created(){
        this.$axios.post('queryAll',this.$qs.stringify({})).then(
            (res)=>{
                this.bookData=res.data
            }
        )
    },
    data(){
        return{
            page:1,
            bookData:[
                {
                    uid:'11',//上传者id
                    lid:'2',//课程、资料  id
                    lscore:'20',//20-课程  10-资料
                    url:'',
                    success:'2', //2- 未通过  1-通过  0-审核中
                    uploadTime:'2021-02-3',//上传时间
                    classify:'关于擎天',//一级类目
                    classify:'发展历程',//二级类目
                    page:'',//封面url
                    title:'第一点一点一滴',//标题
                    text:'22222',//简介
                    url1:[],
                    url2:[],
                    url3:[],
                },
                {
                    uid:'2231',//上传者id
                    lid:'1',//课程、资料  id
                    lscore:'20',//20-课程  10-资料
                    url:'',
                    success:'2', //2- 未通过  1-通过  0-审核中
                    uploadTime:'2021-5-31',//上传时间
                    classify:'产品技术',//一级类目
                    classify:'合作伙伴',//二级类目
                    page:'',//封面url
                    title:'请问我去额为',//标题
                    text:'三大打算打打',//简介
                    url1:[],
                    url2:[],
                    url3:[],
                },                
            ],

        }
    },
    methods:{
        openee(val){
            console.log("---===---")
            // let aaaa = this.$router.resolve({path:'book_kc',query:{id:val.id,score:val.score}})
            // window.open(aaaa.href,"_blank")
            let aaa = this.$router.resolve({path:'book_kc',query:{id:val.id}})
            window.open(aaa.href,"_blank")
        },
        handleCurrentChange(val){
            this.page=val
            console.log(typeof(this.page)+":"+this.page)
        },
    }
}
</script>
<style scoped>
#footer{
position:fixed;
left:20%;
bottom:10px;
height:30px;
width:100%;
}
</style>


