<template>
  <div>
    <!-- 网页头 -->
    <el-row>
      <el-col :span="5"><el-button type="success">立即参加</el-button></el-col>
      <el-col :span="5" :offset="2"
        ><el-button type="success" icon="el-icon-lollipop">收藏</el-button
        ></el-col
      >
      <el-col :span="5" :offset="5">欢迎登录，{{ user.name }}</el-col>
    </el-row>
    <!-- 视频区 -->
    <el-row class="row_1">
      <el-col :span="15" :offset="2">
        <video
          src="@/assets/video/a.mp4"
          width="500px"
          height="300px"
          controls
        ></video>
      </el-col>
      <el-col :span="4" :offset="2">
        <el-menu
          default-active="1"
          class="el-menu-vertical-demo"
          style="width:99%;margin-top:10px;overflow:scroll;height:300px"
          unique-opened
          @select="videoSelect">
          <el-menu-item index="1">视频1</el-menu-item>
          <el-menu-item index="2">视频2</el-menu-item>
          <el-menu-item index="3">视频3</el-menu-item>
          <el-menu-item index="4">视频4</el-menu-item>
          <el-menu-item index="1">视频1</el-menu-item>
          <el-menu-item index="2">视频2</el-menu-item>
          <el-menu-item index="3">视频3</el-menu-item>
          <el-menu-item index="4">视频4</el-menu-item>
          <el-menu-item index="1">视频1</el-menu-item>
          <el-menu-item index="2">视频2</el-menu-item>
          <el-menu-item index="3">视频3</el-menu-item>
          <el-menu-item index="4">视频4</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>

    <el-row>
      <el-tabs v-model="aa">
        <el-tab-pane label="文档" name="1">
          <h3>aaa</h3><h3>aaa</h3><h3>aaa</h3><h3>aaa</h3>
        </el-tab-pane>
        <el-tab-pane label="音频" name="2">
          <h3>aaa</h3><h3>aaa</h3><h3>aaa</h3>
        </el-tab-pane>
        <el-tab-pane label="简介" name="3">
          <h3>aaa</h3><h3>aaa</h3><h3>aaa</h3><h3>aaa</h3>
        </el-tab-pane>
        <el-tab-pane label="评论" name="4">
          <h3>aaa</h3><h3>aaa</h3><h3>aaa</h3>
        </el-tab-pane>
      </el-tabs>
    </el-row>
  </div>
</template>
<script>
import comment from "../../other/comment.vue";
import Jianjie from "../../other/jianjie.vue";
export default {
  components: { comment, Jianjie },
  methods: {
    videoSelect(index, indexpath) {
      // 换视频  index就是菜单的index
      console.log(index);
    },
    selectChange(key, keyPath) {
      console.log(key);
      console.log(keyPath);
      this.change = key == "1" ? true : false;
    }
  },
  data() {
    return {
      aa : '3',
      //通过id、score获取课程具体信息
      id: "",
      score: 0,
      videoSrc: "@/assets/video/a.mp4",
      user: {},
      change: true
    };
  },
  created() {
    console.log("----------------" + this.$route.query.id);
    // this.id='1111'
    // this.score=30
    this.user.name = sessionStorage.getItem("name");
    this.user.auth = sessionStorage.getItem("auth");
    // this.id = sessionStorage.getItem("book_id")
    // this.score = sessionStorage.getItem('book_score')
    this.id = this.$route.query.id;
    this.score = this.$route.query.score;
    console.log(this.id + this.score);
    // console.log(sessionStorage.getItem("book_id"))
  }
};
</script>
<style scoped>
.row_1 {
  background-color: lemonchiffon;
  margin-top: 20px;
}
.row_2 {
  margin-top: 20px;
  background-color: lightgreen;
}
.row_3 {
  margin-top: 20px;
  background-color: rgb(192, 216, 192);
}
.row_4 {
  margin-top: 20px;
  background-color: rgb(175, 220, 247);
}
</style>



