<template>
    <div>
        <!-- 封面 -->
        <el-row>
            <el-col :span="4">封面</el-col>
            <el-col :span="10">
                <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-success="handleSuccess"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-error="handleError"
                    :before-remove="beforeRemove"
                    :on-progress="handleProgress"
                
                    :limit="1"
                    :on-exceed="handleExceed"
                    :file-list="fengmianList"
                    :auto-upload="false">
                    <el-button  slot="trigger" size="small" type="primary" >点击上传</el-button>
                    <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
            </el-col>
        </el-row>
        <!-- 名称 -->
        <el-row>
            <el-col :span="4">名称</el-col>
            <el-col :span="10">
                <el-input v-model="name"></el-input>
            </el-col>
        </el-row>
        <!-- 简介 -->
        <el-row>
            <el-col :span="4">简介</el-col>
            <el-col :span="10">
                <el-input type="textarea" v-model="text"></el-input>
            </el-col>
        </el-row>
         <!-- 视频 -->
        <el-row>
            <el-col :span="4">视频</el-col>
            <el-col :span="10">
            </el-col>
        </el-row>
        <!-- 音频 -->
        <el-row>
            <el-col :span="4">音频</el-col>
            <el-col :span="10">
            
            </el-col>
        </el-row>
        <!-- 文档 -->
        <el-row>
            <el-col :span="4">文档</el-col>
            <el-col :span="10">
               
            </el-col>
        </el-row>
        <!-- 图片 -->
        <el-row>
            <el-col :span="4">图片</el-col>
            <el-col :span="10">
               
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
     data(){
        return{
            fengmianList:[],
            fengmianUrl:'file:///C:/Users/%E7%8E%8B%E6%99%93%E4%BC%9F/Desktop/project/studying/',
            imageUrl: '',
            name:'',
            text:'',
            videoList:[],
            audioList:[],
            docList:[],
            imgList:[{name:'a.jpg',url:'aaa'},{name:'a.jpg',url:'aaa'},{name:'a.jpg',url:'aaa'}],
        }
    },
    methods:{
        submitUpload() {
            console.log("手动提交")
        this.$refs.upload.submit();
      },
      handlePreview(file) {
        console.log("文件列表——预览")
        console.log(file);
      },
      handleExceed(files, fileList) {
          console.log("文件列表——超量")
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
          console.log("文件列表——删除前")
          console.log(file)
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      handleRemove(file, fileList) {
        console.log("文件列表——删除")
        console.log(file, fileList);
      },
      handleSuccess(){
          console.log("文件上传——成功")
      },
      handleError(file,fileList){
          console.log("文件上传——失败")
      },
      handleProgress(file,fileList){
        //   console.log("文件上传——过程")
      }
    }
}
</script>
<style scoped>
.el-row{
    margin-top:30px
}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


