<template>
<div>
    <el-col :span="15">
<el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="邮箱" prop="email">
        <el-input v-model="form.email"></el-input>
    </el-form-item>
  <el-form-item label="验证码" prop="code">
    <el-input v-model="form.code" >
        <i slot="suffix" class="el-input__icon el-icon-search" @click="getCode"></i>
    </el-input>
  </el-form-item>
  <el-form-item label="新密码" prop="password">
        <el-input v-model="form.password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="password2">
        <el-input v-model="form.password2"></el-input>
    </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">修改</el-button>
    <el-button @click="reset">重置</el-button>
  </el-form-item>
</el-form>
</el-col>
</div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          email:'',
          name: '王晓伟',
          department:'研发',
          code:'',
          yanzhengma:'',
          password:'',
          password2:'',
        }
      }
    },
    methods: {
      onSubmit() {
        //   判断验证码
        if(this.form.code!=this.form.yanzhengma){
          alert("验证码错误")
        }else if(this.form.password!=this.form.password2){
            alert("两次密码不匹配")
        }else{
          this.$axios.post('modify',this.$qs.stringify({uname:this.form.name,upwd:this.form.password})).then(
            (res)=>{
              if(res.data=='修改成功'){
                alert(res.data)
              }else if(res.data=='修改失败'){
                alert(res.data)
              }else{
                alert(res.data)
              }
            }
          )
        }
        console.log('submit!');
      },
      reset(){
          this.$refs.forma.resetFields()
      },
      getCode(){
        console.log(this.form)
        this.$axios.post("getCode",this.$qs.stringify({email:this.form.email})  )
            .then((res)=>{
                this.form.yanzhengma =res.data
                console.log("结果："+res.data)
            }).catch((error)=>{
            console.log(error)
        })
      }
    }
  }
</script>


