<template>
    <div>
        <el-row></el-row>
        <el-row>
            <el-table :data="dataList">
                <el-table-column type="index" :index="defaultIndex"></el-table-column>
                <el-table-column label="用户ID"></el-table-column>
                <el-table-column label="用户名"></el-table-column>
                <el-table-column label="获得积分值"></el-table-column>
                <el-table-column label="时间"></el-table-column>
                <el-table-column label="积分来源"></el-table-column>
  
            </el-table>
        </el-row>
        <el-row>
            <div id="footer">
                <el-pagination
                    background
                    :page-size="4"
                    :pager-count="7"
                    :total="100"
                    @current-change="handleCurrentChange"
                    layout="total,prev, pager, next,jumper">
                </el-pagination>
        </div>
        </el-row>
    </div>
</template>
<script>
export default {
    created(){

    },
    data(){
        return{
            page:1,
            dataList:[
                {
                    uid:'1',
                    uname:'',
                    getScore:'',
                    getTime:'',
                    from:'',
                }
            ]
        }
    },
    methods:{
        getData(){
            this.$axios.post('queryUserScoreFrom',this.$qs.stringify({})).then(
                (res)=>{
                    this.dataList=res.data
                }
            )
        },
        defaultIndex(index){
            return index+1
        },
        handleCurrentChange(val){
            this.page=val
            console.log(typeof(this.page)+":"+this.page)
        },
    }
}
</script>
<style scoped>
#footer{
position:fixed;
left:20%;
bottom:10px;
height:30px;
width:100%;
}
</style>


