<template>
<div>
    <el-col :span="15">
<el-form ref="forma" :model="form" label-width="80px">
    <el-form-item label="邮箱" prop="email">
        <el-input v-model="form.email" disabled></el-input>
    </el-form-item>
  <el-form-item label="用户名" prop="uname">
    <el-input v-model="form.uname" disabled></el-input>
  </el-form-item>
  <el-form-item label="头像">
      <img src="@/assets/sky.png">
  </el-form-item>
  <el-form-item label="部门" prop="department">
      <el-select v-model="form.department">
          <el-option label="研发1" value="研发1"></el-option>
          <el-option label="研发2" value="研发2"></el-option>
          <el-option label="研发3" value="研发3"></el-option>
          <el-option label="研发4" value="研发4"></el-option>
      </el-select>
    <!-- <el-input v-model="form.department"></el-input> -->
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">修改</el-button>
    <el-button @click="reset">重置</el-button>
  </el-form-item>
</el-form>
</el-col>
</div>
</template>
<script>
  export default {
    created(){
      // 获取我的信息
      // this.form.email=sessionStorage.getItem('email')
      // this.form.uname=sessionStorage.getItem('uname')
      // this.form.department=sessionStorage.getItem('dept')
      // this.form.uimg=sessionStorage.getItem('uimg')
    },
    data(){
      return {
        form: {
          email:'93021@qq.com',
          uname: '王晓伟',
          department:'研发',
          uimg:'',
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      reset(){
          this.$refs.forma.resetFields()
      }
    }
  }
</script>


